</template>

<script>
export default {
  data() {
    return {
      isHovering: "",
      toggleEl: false,
    };
  },
  watch: {
    toggleEl(newVal, oldVal) {
      const links = document.getElementsByClassName("links-column");
      if (newVal) {
        for (let i = 0; i < links.length; i++) {
          links[i].style.opacity = 1;
        }
      } else {
        for (let i = 0; i < links.length; i++) {
          links[i].style.opacity = 0;
        }
      }
    },
  },
  methods: {
    goTo(link) {
      const a = document.createElement("a");

      if (link === "tanweishen97@gmail.com") {
        a.href = "mailto:" + link;
      } else {
        a.href = link;
      }

      a.target = "_blank";
      a.click();
    },
    highlight(id) {
      this.isHovering = id;
    },
    resetHighlight() {
      this.isHovering = "";
    },
    elColor(id) {
      if (id === this.isHovering) {
        return "#30DEFF";
      }
      return "#c7e4e9";
    },
  },
  mounted() {
    // eslint-disable-next-line
    const observer = new IntersectionObserver((entries) => {
      if (entries[0].intersectionRatio > 0) {
        this.toggleEl = true;
        this.$store.commit("setExternalLinks", false);
        console.log("Cross Contact");
      } else {
        this.toggleEl = false;
        this.$store.commit("setExternalLinks", true);
      }
    });
    observer.observe(document.querySelector("#contact"));
  },
};
</script>

<style lang="sass" scoped>
#contact
  width: 100%
  position: relative
  padding: 100px 0px

.content-container
  position: relative
  width: 100%
  max-width: 1024px
  margin: auto

.heading
  text-align: center
  p
    font-size: 42px
    font-weight: bold
    color: #30DEFF
  .heading-line
    width: 400px
    background-color: #30DEFF
    margin: auto
    height: 1px

.inner-content
  padding-top: 20px
  .links-column
    display: flex
    max-width: 600px
    margin: auto
    cursor: pointer
    align-items: center
    transition: opacity 1s ease
    opacity: 0
    .link
      flex-grow: 0
      width: 100px
      height: 24px
      margin: 10px
      margin-left: 30px
      cursor: pointer
      transition: all 0.7s ease
      img
        width: 24px

#link-1
  transition-delay: 0.2s

#link-2
  transition-delay: 0.4s

#link-3
  transition-delay: 0.6s

#link-4
  transition-delay: 0.8s

// .text-link
//   color: #c7e4e9

// .text-link:hover
//   color: #30DEFF
</style>
